.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.user-section {
  width: 100%;
  padding: 20px 0;
  box-sizing: border-box;
  display: flex;
  border-radius: 5px;
}

.user-section img {
  border-radius: 100px;
  margin-right: 25px;
  position: relative;
}

.user-section img::after {
  content: "";
  width: 10px;
  height: 10px;
  background-color: #31b96e;
  border-radius: 100%;
  border: 1px solid #fff;
  position: absolute;
}
